import React, {createContext, useEffect} from "react";
import {HeaderWidget} from "./widget/header";
import styled from "styled-components";
import {ClaimAirdropWidget} from "./widget/claim_airdrop";
import {MyProgressWidget} from "./widget/my_progress";
import {MissionsWidget} from "./widget/missions";
import {HttpStatus, useWait} from "../utils/wait_fn";
import {Server} from "../net/server";
import {useWeb3React} from "@web3-react/core";
import {TaskModel} from "../model/task_model"
import {Provider} from "react-redux";
import {TopTisWidget} from "./widget/tis";
import {ModelProvider} from "../provider/model_provider";

export const HomeWidget: React.FC = () => {

    const {account} = useWeb3React()
    const {request, status, data} = useWait<TaskModel>(Server.task);

    useEffect(() => {
        request(account)
    }, [account])

    return (
        <HomeStyle>
            <ModelProvider>
                <HomeContext.Provider value={{
                    httpStatus: status,
                    taskModel: data
                }}>
                    <HeaderWidget/>
                    <TopTisWidget/>
                    <div style={{height: '30px'}}/>
                    <ClaimAirdropWidget/>
                    <MyProgressWidget/>
                    <MissionsWidget/>
                </HomeContext.Provider>
            </ModelProvider>



        </HomeStyle>
    );
}


export interface HomeContextInter {
    taskModel?: TaskModel,
    httpStatus: HttpStatus
}

export const HomeContext = createContext<HomeContextInter>({
    httpStatus: HttpStatus.start
})

const HomeStyle = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  font-family: Cabin;
`
