import React, {useContext} from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import {HomeContext, HomeContextInter} from "../home";
import Countdown, {zeroPad} from "react-countdown";
import {useAirDropContract} from "../../hook/useContract";
import {useSingleCallResult} from "../../state/multicall/hooks";
import {useWeb3React} from "@web3-react/core";
import {getBalanceFormat} from "../../utils/formatBalance";
import {useSendModal} from "./modal/send_modal";

export const TopTisWidget: React.FC = () => {

    const {account} = useWeb3React();
    const airDropContract = useAirDropContract();
    const infos = useSingleCallResult(airDropContract, 'getInfos', [account ?? '']).result;
    const claim = useSendModal(async () => {
        if (airDropContract)
            await airDropContract.claim()
    });
    return (
        <ClaimAirdropStyle>
            <h2>Claim Airdrop</h2>
            <Grid container spacing={4}>
                <Grid item sm={4}>
                    <BoxStyle>
                        <h3>
                            您拥有的奖励
                        </h3>
                        <div className={'wap'}>
                            <div>
                                <h4>
                                    {getBalanceFormat(infos?.reward_.toString())}
                                </h4>
                                <h5>
                                    Sunder
                                </h5>
                            </div>

                            <p onClick={() => claim()}>领取</p>
                        </div>

                    </BoxStyle>
                </Grid>
                <Grid item sm={4}>
                    <BoxStyle>
                        <h3>
                            锁定
                        </h3>
                        <h4>
                            {getBalanceFormat(infos?.total_.mul(infos?.claim_).toString())}

                        </h4>
                        <h5>
                            OSMO
                        </h5>
                    </BoxStyle>
                </Grid>
                <Grid item sm={4}>
                    <BoxStyle>
                        <h3>剩余时间</h3>
                        <h4>
                            {infos?.endTime_ &&
                            <Countdown renderer={renderer} date={new Date(infos?.endTime_ * 1000).toUTCString()}/>}
                        </h4>
                        <h5>

                        </h5>
                    </BoxStyle>
                </Grid>
            </Grid>
        </ClaimAirdropStyle>
    );
}

// @ts-ignore
const renderer = ({days, hours, minutes, seconds, completed}) => {

    return (
        <>
            <div>

                <div>
                    <h5>{zeroPad(days)}</h5>
                    <p>D</p>
                </div>
                <div>
                    <h5>{zeroPad(hours)}</h5>
                    <p>H</p>
                </div>
                <div>
                    <h5>{zeroPad(minutes)}</h5>
                    <p>M</p>
                </div>

            </div>
        </>
    );
};

